import { useState } from "react";
import ReactCardFlip from "react-card-flip";
import { Box, Typography, Button, TextField, InputBase } from "@mui/material";

const FlipCard = (props) => {
  const [isFlipped, setIsFlipped] = useState(false);
  const [teamName, setTeamName] = useState("");

  const handleClick = (e) => {
    e.preventDefault();
    setIsFlipped(!isFlipped);
  };
  const handleComplete = (e) => {
    e.preventDefault();
    e.stopPropagation();
    props.completeCard(props.index, teamName);
    setTeamName("");
  };
  return (
    <ReactCardFlip isFlipped={isFlipped} flipDirection="vertical">
      <Box className="challenge_card space_between" onClick={handleClick}>
        <Box>
          <Typography className="f_xl fw_500 ff_md c_card ls_ss mt_1">
            {props.name}
          </Typography>
          <Typography className="f_lg fw_500 ff_md c_secondary ls_ss mt_1">
            {props.distance}
          </Typography>
        </Box>
        <Box className="d_flex flex_card">
          {props.completedIndices.hasOwnProperty(props.index) === true ? (
            <>
              <Typography className="f_lg ff_md c_secondary ls_ss mt_1">
                Completed by: {props.completedIndices[props.index]}
              </Typography>
            </>
          ) : (
            <>
              <InputBase
                id="outlined-basic"
                label="Team Name"
                className="default_textfield mt_1"
                color="warning"
                placeholder="Enter Team Name"
                value={teamName}
                onChange={(e) => setTeamName(e.target.value)}
                onClick={(e) => {
                  e.preventDefault();
                  e.stopPropagation();
                }}
              />
              <Button
                variant="contained"
                className="default_btn mt_1"
                onClick={handleComplete}
              >
                <Typography className="f_maldini mt_05">Complete</Typography>
              </Button>{" "}
            </>
          )}
        </Box>
      </Box>

      <Box className="challenge_card p_1 space_between" onClick={handleClick}>
        <Typography className="f_lg ff_md ls_sm">
          {props.description}
        </Typography>
      </Box>
    </ReactCardFlip>
  );
};

export default FlipCard;
