import { getDatabase, ref, set, onValue, update, get } from "firebase/database";
import { initializeApp } from "firebase/app";

const firebaseConfig = {
  apiKey: "AIzaSyCZSgaTUdWA9xKjmlsiY7wYryt4yiQeUys",
  authDomain: "bartlag.firebaseapp.com",
  databaseURL: "https://bartlag-default-rtdb.firebaseio.com",
  projectId: "bartlag",
  storageBucket: "bartlag.appspot.com",
  messagingSenderId: "131349116760",
  appId: "1:131349116760:web:1a8d3a8dd992beb996572d",
  measurementId: "G-P8Q5CE337R",
};

const app = initializeApp(firebaseConfig);
const db = getDatabase(app);
const dbRef = ref(db, "currentIndices");
const completedRef = ref(db, "completedIndices");

function generateIndices(completedIndices) {
  const availableIndices = Array.from({ length: 26 }, (_, i) => i).filter(
    (index) => !completedIndices.includes(index)
  );

  const indices = [];
  while (indices.length < 5 && availableIndices.length > 0) {
    const randomIndex = Math.floor(Math.random() * availableIndices.length);
    const index = availableIndices[randomIndex];
    indices.push(index);
    availableIndices.splice(randomIndex, 1);
  }

  return indices;
}

function updateDb(indices) {
  const updates = {
    indices: indices.join(","),
    lastUpdatedTime: new Date().getTime(),
  };
  set(dbRef, updates);
}

function Admin() {
  const resetGame = () => {
    const completedRef = ref(db, "completedIndices");
    set(completedRef, null);

    const availableIndices = Array.from({ length: 26 }, (_, i) => i);
    const newIndices = [];
    while (newIndices.length < 5 && availableIndices.length > 0) {
      const randomIndex = Math.floor(Math.random() * availableIndices.length);
      const index = availableIndices[randomIndex];
      newIndices.push(index);
      availableIndices.splice(randomIndex, 1);
    }

    const dbRef = ref(db, "currentIndices");
    const updates = {
      indices: newIndices.join(","),
      lastUpdatedTime: new Date().getTime(),
    };
    set(dbRef, updates);
  };

  const rerollIndices = async () => {
    const snapshot = await get(dbRef);
    const data = snapshot.val();
    const currentIndices = data.indices.split(",").map(Number);

    const completedSnapshot = await get(completedRef);
    const completedData = completedSnapshot.val() || {};
    const completedIndices = Object.keys(completedData).map(Number);

    const newIndices = generateIndices([
      ...completedIndices,
      ...currentIndices,
    ]);
    updateDb(newIndices);

    const completedUpdates = {};
    currentIndices.forEach((index) => {
      if (!completedData.hasOwnProperty(index)) {
        completedUpdates[index] = "completed";
      }
    });
    console.log(completedUpdates);
    update(completedRef, completedUpdates);
  };
  return (
    <div>
      <h1>Admin</h1>
      <button onClick={resetGame}>Reset Game</button>
      <button onClick={rerollIndices}>Reroll Indices</button>
    </div>
  );
}

export default Admin;
